.footer-wrap { padding: 30px 0; background: darkblue;
    text-align: center;
    ul { list-style: none;
        li { display: inline-block; padding: 0 5px;
            a { color: #fff; text-decoration: none;}
            a:hover{ color: gray;}
        }
        li:before {
            content: '- ';
        }
        li:first-child:before { display: none;}
    }
    p {
        span {
            a { color: #fff; text-decoration: none;}
            a:hover {color: gray;}
        }
    }
}